import { Card, CardBody, FormGroup, Label, Input, CustomInput, Button, Row, Col } from 'reactstrap'
import { PublicKey } from '@solana/web3.js'
import { Program, Provider, web3, BN } from '@project-serum/anchor'
import { TOKEN_PROGRAM_ID, Token } from "@solana/spl-token"
import idl from '../../../../json/idl.json'
import poolSecret from '../../../../json/pool.json'
import "../../../../assets/scss/views/staking/staking.scss"
import {
  useWallet,
  useConnection
} from '@solana/wallet-adapter-react'
import axios from "axios"
import { useEffect, useState } from 'react'
require('@solana/wallet-adapter-react-ui/styles.css')
const anchor = require('@project-serum/anchor')
const { SystemProgram, Keypair, SYSVAR_CLOCK_PUBKEY } = web3
const programID = new PublicKey("E4VP5CqmKCy7HLzCyZg3FaKLoCE5M9nLhyZLWPMjeeQ6")

const poolKey = new PublicKey(new Uint8Array(poolSecret))

const opts = {
  preflightCommitment: "processed"
}
const StakingContent = () => {
  const { publicKey } = useWallet()
  const wallet = useWallet()
  const { connection } = useConnection()
  const [balance, setBalance] = useState(0.0)
  const [depositVal, setDepositVal] = useState(0)
  const [withdrawVal, setWithdrawVal] = useState(0)
  const [rewardAmount, setRewardAmount] = useState(0)
  const [usercount, setUsercount] = useState(0)

  const getAccontData = async () => {
    const url = 'https://admin.solview.app/staking/init'
    const accData = await axios.get(url)
    if (accData.data.data) {
      setUsercount(accData.data.data.length)
    }
  }

  const getProvider = async () => {
    const provider = new Provider(
      connection, wallet, opts.preflightCommitment
    )
    return provider
  }

  const getWalletBalance = async (pubkey) => {
    if (!wallet.publicKey) {
      return 0
    }

    const provider = await getProvider()
    const tokens = await provider.connection.getTokenAccountsByOwner(wallet.publicKey, { mint: pubkey })
    if (tokens.value.length === 0) {
      return 0
    }
    const token = tokens.value.pop()
    const val = (await provider.connection.getTokenAccountBalance(token.pubkey)).value
    setBalance(val.uiAmount)
  }
  const getAllData = async () => {
    const provider = await getProvider()
    const program = new Program(idl, programID, provider)
    const poolObject = await program.account.pool.fetch(poolKey)
    setRewardAmount(poolObject.adminRewardAmount.toNumber() / (10 ** 6))
  }

  const authorizeFunder = async (newFunder) => {
    const provider = await getProvider()
    const program = new Program(idl, programID, provider)
    const pubkeyToUse = poolKey.publi
    await program.rpc.authorizeFunder(
      newFunder,
      {
        accounts: {
          pool: pubkeyToUse,
          authority: provider.wallet.publicKey
        }
      })
  }

  const deauthorizeFunder = async (oldFunder) => {
    const provider = await getProvider()
    const program = new Program(idl, programID, provider)
    const pubkeyToUse = poolKey
    await program.rpc.deauthorizeFunder(
      oldFunder,
      {
        accounts: {
          pool: pubkeyToUse,
          authority: provider.wallet.publicKey
        }
      })
  }

  const fund = async (amount) => {
    if (amount === 0) {
      alert("Balance is 0!")
      return
    }
    if (amount > balance) {
      alert("This is more than your balance!")
      return
    }
    const provider = await getProvider()
    const program = new Program(idl, programID, provider)
    const pubkeyToUse = poolKey
    const poolObject = await program.account.pool.fetch(pubkeyToUse)

    const stakingMintObject = new Token(
      provider.connection,
      new PublicKey('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN'),
      TOKEN_PROGRAM_ID,
      provider.wallet.payer)
    const stakingAccountInfo = await stakingMintObject.getOrCreateAssociatedAccountInfo(wallet.publicKey)
    const stakingPubkey = stakingAccountInfo.address
    const [
      _poolSigner,
      _nonce
    ] = await PublicKey.findProgramAddress(
      [pubkeyToUse.toBuffer()],
      programID
    )
    const poolSigner = _poolSigner
    try {
      await program.rpc.fundStaking(
        new BN(amount * (10 ** 6)),
        {
          accounts: {
            // Stake instance.
            pool: pubkeyToUse,
            stakingVault: poolObject.stakingVault,
            funder: provider.wallet.publicKey,
            from: stakingPubkey,
            // Program signers.
            poolSigner,
            // Misc.
            tokenProgram: TOKEN_PROGRAM_ID
          }
        })
      getAllData()
      getWalletBalance(new PublicKey("7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN"))
    } catch (err) {
      console.log(err)
    }
  }

  const withdraw = async (amount) => {
    const provider = await getProvider()

    if (amount === 0) {
      alert("Balance is 0!")
      return
    }
    if (amount > rewardAmount) {
      alert("This is more than your balance!")
      return
    }


    const program = new Program(idl, programID, provider)
    const pubkeyToUse = poolKey
    const poolObject = await program.account.pool.fetch(pubkeyToUse)

    const stakingMintObject = new Token(
      provider.connection,
      new PublicKey('7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN'),
      TOKEN_PROGRAM_ID,
      provider.wallet.payer)
    const stakingAccountInfo = await stakingMintObject.getOrCreateAssociatedAccountInfo(wallet.publicKey)
    const stakingPubkey = stakingAccountInfo.address

    const [
      _poolSigner,
      _nonce
    ] = await PublicKey.findProgramAddress(
      [pubkeyToUse.toBuffer()],
      programID
    )
    const poolSigner = _poolSigner
    try {

      await program.rpc.withdraw(
        new BN(amount * (10 ** 6)),
        {
          accounts: {
            // Stake instance.
            pool: pubkeyToUse,
            stakingVault: poolObject.stakingVault,
            funder: provider.wallet.publicKey,
            from: stakingPubkey,
            // Program signers.
            poolSigner,
            // Misc.
            tokenProgram: TOKEN_PROGRAM_ID
          }
        })
      getAllData()
      getWalletBalance(new PublicKey("7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN"))
    } catch (err) {
      console.log(err)
    }

  }

  useEffect(() => {
    if (publicKey) {
      getAccontData()
      getAllData()
      getWalletBalance(new PublicKey("7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN"))
    }
  }, [publicKey])
  return (
    <Card>
      <CardBody>
        <Row>
          <Col md='4' sm='12'>
            <Label id="solvBalance">
              SOLV Balance: {balance ? balance.toFixed(4) : 0} SOLV
            </Label>
          </Col>
          <Col md='4' sm='12'>
            <Label id="userCount">
              Users Count: {usercount}
            </Label>
          </Col>
          <Col md="4" sm="12">
            <Label id="rewardAmount">
              Reward Amount: {rewardAmount ? rewardAmount.toFixed(4) : 0} SOLV
            </Label>
          </Col>
        </Row>
        <Row id="RewardManage">
          <Col md='6' sm='12'>
            <Row>
              <Col md="12" sm="12">
                <FormGroup>
                  <Label for='input' id="depositeAmt">Deposit</Label>
                  <Input type='text' value={depositVal} onChange={(e) => setDepositVal(e.target.value)} id="url" />
                </FormGroup>
              </Col>
              <Col md="12" sm="12">
                <Row>
                  <Col md="9" sm="9" id="maxBtn">
                    <Button onClick={() => setDepositVal(balance)}>MAX</Button>
                  </Col>
                  <Col md="3" sm="3" id="approveBtn">
                    <Button onClick={() => fund(depositVal)}>APPROVE</Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
          <Col md='6' sm='12'>
            <Row>
              <Col me="12" sm="12">
                <FormGroup>
                  <Label for='input' id="withdrawAmt">Withdraw</Label>
                  <Input type='text' value={withdrawVal} onChange={(e) => setWithdrawVal(e.target.value)} id="url" />
                </FormGroup>
              </Col>
              <Col md="12" sm="12">
                <Row>
                  <Col md="9" sm="9" id="maxBtn">
                    <Button onClick={() => setWithdrawVal(rewardAmount)}>MAX</Button>
                  </Col>
                  <Col md="3" sm="3" id="approveBtn">
                    <Button onClick={() => withdraw(withdrawVal)}>APPROVE</Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>

        </Row>

      </CardBody>
    </Card>
  )
}

export default StakingContent
