// ** React Imports
import { Fragment, useState, useEffect, memo } from 'react'

// ** Table Column
import { serverSideColumns } from './data'
import idl from '../../../../json/idl.json'
import poolSecret from '../../../../json/pool.json'
// ** Store & Actions
import { getData } from './store/actions'
import { useSelector, useDispatch } from 'react-redux'
import axios from "axios"
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, CardHeader, CardTitle, Input, Label, Row, Col } from 'reactstrap'
import { PublicKey } from '@solana/web3.js'
import { Program, Provider, web3, BN } from '@project-serum/anchor'
import { TOKEN_PROGRAM_ID, Token } from "@solana/spl-token"
import {
  useWallet,
  useConnection
} from '@solana/wallet-adapter-react'
require('@solana/wallet-adapter-react-ui/styles.css')
const { SystemProgram, Keypair, SYSVAR_CLOCK_PUBKEY } = web3
const programID = new PublicKey("E4VP5CqmKCy7HLzCyZg3FaKLoCE5M9nLhyZLWPMjeeQ6")

const poolKey = new PublicKey(new Uint8Array(poolSecret))
const opts = {
  preflightCommitment: "processed"
}
const DataTableServerSide = () => {
  // ** Store Vars
  const { publicKey } = useWallet()
  const wallet = useWallet()
  const { connection } = useConnection()
  const dispatch = useDispatch()
  const store = useSelector(state => state.dataTables)

  // ** States
  const [currentPage, setCurrentPage] = useState(0)
  const [rowsPerPage, setRowsPerPage] = useState(7)
  const [dataCount, setDataCount] = useState(0)
  const [searchValue, setSearchValue] = useState('')
  const [stakedData, setStakedData] = useState([])
  // ** Get data on mount
  useEffect(() => {
    dispatch(
      getData({
        page: currentPage,
        perPage: rowsPerPage,
        q: searchValue
      })
    )
  }, [dispatch])
  const getProvider = async () => {
    const provider = new Provider(
      connection, wallet, opts.preflightCommitment
    )
    return provider
  }


  const getAccontData = async (curPage) => {
    console.log(curPage, "curr")
    const url = 'https://admin.solview.app/staking/init'
    const accData = await axios.get(url)
    const provider = await getProvider()
    const program = new Program(idl, programID, provider)

    if (accData.data.data) {
      setDataCount(accData.data.data.length)
      const userData = []
      for (let i = (curPage * 10); i < (((curPage + 1) * 10) - 1); i++) {
        try {
          const data = accData.data.data[i]
          const userOne = {}
          const [_userPubkey, _userNonce] = await PublicKey.findProgramAddress(
            [(new PublicKey(accData.data.data[i].account)).toBuffer(), poolKey.toBuffer()],
            programID
          )
          const Adata = await program.account.user.fetch(_userPubkey)
          userOne.account = data.account
          userOne.staked_amount_7 = Adata.balanceStakedA.toNumber() / (10 ** 6)
          userOne.staked_amount_14 = Adata.balanceStakedB.toNumber() / (10 ** 6)
          userOne.staked_amount_30 = Adata.balanceStakedC.toNumber() / (10 ** 6)
          userOne.staked_time_7 = (new Date(Adata.stakeTimeA.toNumber() * 1000)).toISOString()
          userOne.staked_time_14 = (new Date(Adata.stakeTimeB.toNumber() * 1000)).toISOString()
          userOne.staked_time_30 = (new Date(Adata.stakeTimeC.toNumber() * 1000)).toISOString()
          if ((((new Date().getTime()) / 1000) - (Adata.stakeTimeB.toNumber())) > 14 * 24 * 60 * 60) {
            userOne.reward_14 = ((Adata.rewardBRate.toNumber() / (10 ** 6)) * (((new Date().getTime()) / 1000) - (Adata.lastUpdateTimeB.toNumber()))).toFixed(6)
          }
          if ((((new Date().getTime()) / 1000) - (Adata.stakeTimeC.toNumber())) > 30 * 24 * 60 * 60) {
            userOne.reward_30 = ((Adata.rewardCRate.toNumber() / (10 ** 6)) * (((new Date().getTime()) / 1000) - (Adata.lastUpdateTimeC.toNumber()))).toFixed(6)
          }
          userData.push(userOne)
        } catch (e) {
          console.log(e)
        }
      }
      console.log(userData, "userData")
      setStakedData(userData)
    }
  }

  useEffect(() => {
    getAccontData(0)
  }, [])
  // ** Function to handle filter
  const handleFilter = e => {
    setSearchValue(e.target.value)

    dispatch(
      getData({
        page: currentPage,
        perPage: rowsPerPage,
        q: e.target.value
      })
    )
  }

  // ** Function to handle Pagination and get data
  const handlePagination = page => {
    console.log(page, "page")
    // dispatch(
    //   getData({
    //     page: page.selected + 1,
    //     perPage: rowsPerPage,
    //     q: searchValue
    //   })
    // )
    setCurrentPage(page.selected + 1)
    getAccontData(page.selected)
  }

  // ** Function to handle per page
  const handlePerPage = e => {
    // dispatch(
    //   getData({
    //     page: currentPage,
    //     perPage: parseInt(e.target.value),
    //     q: searchValue
    //   })
    // )
    setRowsPerPage(parseInt(e.target.value))
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number((dataCount / 10).toFixed(0))
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        breakLabel='...'
        pageCount={count || 1}
        marginPagesDisplayed={2}
        pageRangeDisplayed={2}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        breakClassName='page-item'
        breakLinkClassName='page-link'
        containerClassName={
          'pagination react-paginate separated-pagination pagination-sm justify-content-end pr-1 mt-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchValue
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return store.allData.slice(0, rowsPerPage)
    }
  }

  return (
    <Fragment>
      <Card>
        <CardHeader className='border-bottom'>
          <CardTitle tag='h4'>Users</CardTitle>
        </CardHeader>
        {/* <Row className='mx-0 mt-1 mb-50'>
          <Col sm='6'>
            <div className='d-flex align-items-center'>
              <Label for='sort-select'>show</Label>
              <Input
                className='dataTable-select'
                type='select'
                id='sort-select'
                value={rowsPerPage}
                onChange={e => handlePerPage(e)}
              >
                <option value={7}>7</option>
                <option value={10}>10</option>
                <option value={25}>25</option>
                <option value={50}>50</option>
                <option value={75}>75</option>
                <option value={100}>100</option>
              </Input>
              <Label for='sort-select'>entries</Label>
            </div>
          </Col>
          <Col className='d-flex align-items-center justify-content-sm-end mt-sm-0 mt-1' sm='6'>
            <Label className='mr-1' for='search-input'>
              Search
            </Label>
            <Input
              className='dataTable-filter'
              type='text'
              bsSize='sm'
              id='search-input'
              value={searchValue}
              onChange={handleFilter}
            />
          </Col>
        </Row> */}
        <DataTable
          noHeader
          pagination
          paginationServer
          className='react-dataTable'
          columns={serverSideColumns}
          sortIcon={<ChevronDown size={10} />}
          paginationComponent={CustomPagination}
          data={stakedData}
        />
      </Card>
    </Fragment>
  )
}

export default memo(DataTableServerSide)
